}

.submit-btn {
  height: 50px;
  margin-bottom: 16px;
}

.hidden {
  display: none;
}

.section {
  padding-top: 120px;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.modal {
  width: 408px;
  background-color: #fff;
  border-radius: 4px;
  padding: 64px 24px 86px;
  position: absolute;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop.hidden {
  display: none;
}

[data-close] {
  position: absolute;
  top: 24px;
  right: 24px;
}

.form-header {
  margin-bottom: 24px;
}

.signin-form, .login-form {
  flex-direction: column;
  display: flex;
}

.signin.hidden, .login.hidden {
