.have-account button {
  color: #00f;
  background-color: rgba(0, 0, 0, 0);
  margin: 0;
  padding: 0;
}

[data-user] {
  margin-bottom: 8px;
}

.logout-btn {
  padding: 8px;
}

header {
  border-bottom: 1px solid #979797;
}

.top-line {
  align-items: center;
  display: flex;
}

.profile-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.site-nav {
  margin-left: 409px;
}

.site-nav-list {
  gap: 40px;
  display: flex;
}

.site-nav-list .item {
  padding-top: 24px;
